import { useState } from 'react';
// material
import { Box, Step, Paper, Button, Stepper, StepLabel, Typography, StepContent } from '@material-ui/core';

// ----------------------------------------------------------------------

const steps = [
  {
    label: 'Upload your file on Crust Network',
    description: `You should have a small amount of $CRU on your wallet and Crust Wallet installed`
  },
  {
    label: 'Customize the preface of your file',
    description: 'You will get a beautiful QR card after this step.'
  },
  {
    label: 'Upload QR Card and Metadata',
    description: `Upload on Crust Network`
  },
  {
    label: 'Generate NFT',
    description: `Upload to Polygon Smart Contract`
  }
];

export default function VerticalLinearStepper() {
  const [activeStep, setActiveStep] = useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((step, index) => (
          <Step key={step.label}>
            <StepLabel optional={index === 2 ? <Typography variant="caption">Last step</Typography> : null}>
              {step.label}
            </StepLabel>
            <StepContent>
              <Typography>{step.description}</Typography>
              <Box sx={{ mt: 3 }}>
                <Button variant="contained" onClick={handleNext}>
                  {index === steps.length - 1 ? 'Finish' : 'Continue'}
                </Button>
                <Button disabled={index === 0} onClick={handleBack}>
                  Back
                </Button>
              </Box>
            </StepContent>
          </Step>
        ))}
      </Stepper>

      {activeStep === steps.length && (
        <Paper sx={{ p: 3, mt: 3, bgcolor: 'grey.50012' }}>
          <Typography paragraph>Trasaction: 0x123456</Typography>
          <Button onClick={handleReset}>Reset</Button>
        </Paper>
      )}
    </>
  );
}
