// material
import { Container, Typography, Paper, Stack } from '@material-ui/core';
// hooks
import useSettings from '../hooks/useSettings';
// components
import Page from '../components/Page';
// ----------------------------------------------------------------------
import VerticalLinearStepper from '../components/_dashboard/nft-minting/VerticalLinearStepper';
import HorizontalLinearStepper from '../components/_dashboard/nft-minting/HorizontalLinearStepper';

export default function NFTMinting() {
  const { themeStretch } = useSettings();

  return (
    <Page title="NFT Minting">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Stack spacing={5}>
          <Paper
            sx={{
              p: 3,
              width: '100%',
              boxShadow: (theme) => theme.customShadows.z8
            }}
          >
            <HorizontalLinearStepper />
          </Paper>
          <Typography variant="h5" sx={{ pt: 5 }}>
            The UI for mobile (future dev. when Crust Wallet is workable on Mobile)
          </Typography>
          <Paper
            sx={{
              p: 3,
              width: '100%',
              boxShadow: (theme) => theme.customShadows.z8
            }}
          >
            <VerticalLinearStepper />
          </Paper>
        </Stack>
      </Container>
    </Page>
  );
}
